import React, { useState, useRef, useEffect } from 'react';

const SpeechToChineseTextWithRecordingAndUpload = () => {
  const [isRecording, setIsRecording] = useState(false);
  const [transcript, setTranscript] = useState('');
  const [audioBlob, setAudioBlob] = useState(null);
  const mediaRecorderRef = useRef(null);
  const recognitionRef = useRef(null);

  useEffect(() => {
    // 初始化 SpeechRecognition 对象
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (SpeechRecognition) {
      const recognitionInstance = new SpeechRecognition();
      recognitionInstance.continuous = true; // 持续监听
      recognitionInstance.interimResults = true; // 实时结果
      recognitionInstance.lang = 'zh-CN'; // 设置语言为中文

      recognitionInstance.onresult = (event) => {
        let interimTranscript = '';
        for (let i = event.resultIndex; i < event.results.length; i++) {
          const transcriptPart = event.results[i][0].transcript;
          if (event.results[i].isFinal) {
            setTranscript((prev) => prev + transcriptPart);
          } else {
            interimTranscript += transcriptPart;
          }
        }
        setTranscript((prev) => prev + interimTranscript);
      };

      recognitionInstance.onerror = (event) => {
        console.error('语音识别错误:', event.error);
      };

      recognitionRef.current = recognitionInstance;
    } else {
      console.error('浏览器不支持 SpeechRecognition API');
    }
  }, []);

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream);
      const audioChunks = [];

      mediaRecorderRef.current.ondataavailable = (event) => {
        audioChunks.push(event.data);
      };

      mediaRecorderRef.current.onstop = () => {
        const audioBlob = new Blob(audioChunks, { type: 'audio/wav' });
        setAudioBlob(audioBlob);
        console.log('生成的音频 Blob:', audioBlob);

        // 调用上传函数，将 Blob 发送到服务器
        uploadAudioToServer(audioBlob);
      };

      mediaRecorderRef.current.start();
      recognitionRef.current.start(); // 开始语音识别
      setIsRecording(true);
    } catch (error) {
      console.error('录音失败:', error);
    }
  };

  const stopRecording = () => {
    mediaRecorderRef.current.stop();
    recognitionRef.current.stop(); // 停止语音识别
    setIsRecording(false);
  };

  // 上传音频 Blob 到服务器
  const uploadAudioToServer = async (blob) => {
    const formData = new FormData();
    formData.append('audio', blob, 'recording.wav'); // 附加音频文件，文件名为 recording.wav

    try {
      const response = await fetch('/upload', { // 替换为您的服务器上传路径
        method: 'POST',
        body: formData,
      });

      if (response.ok) {
        console.log('音频上传成功');
      } else {
        console.error('音频上传失败');
      }
    } catch (error) {
      console.error('上传音频时发生错误:', error);
    }
  };

  return (
    <div>
      <h1>录音并转为中文文字并上传</h1>
      <button onClick={isRecording ? stopRecording : startRecording}>
        {isRecording ? '停止录音' : '开始录音'}
      </button>
      <p>转录结果：</p>
      <div style={{ border: '1px solid #ccc', padding: '10px', marginTop: '10px' }}>
        {transcript}
      </div>
      {audioBlob && (
        <audio controls>
          <source src={URL.createObjectURL(audioBlob)} type="audio/wav" />
        </audio>
      )}
    </div>
  );
};

export default SpeechToChineseTextWithRecordingAndUpload;
